
#main{
    display: table;
    width: 100%;
    height: 50vh;
    text-align: center;
    color:rgb(161, 153, 153);
}

.fof{
	  display: table-cell;
	  vertical-align: middle;
}

.fof h1{
	  font-size: 50px;
	  display: inline-block;
	  padding-right: 12px;
	  animation: type .5s alternate infinite;
}

@keyframes type{
	  from{box-shadow: inset -3px 0px 0px #888;}
	  to{box-shadow: inset -3px 0px 0px transparent;}
}
.confuse{
    height: 300px;
    width:300px;
    border-radius: 30px;
    margin-bottom: 10px;
}